.welcome-painting {
  // position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.welcome-painting__text {
  width: 320px;
  min-height: 205px;
}

.welcome-painting__sedona-mountains {
  position: relative;  
  box-sizing: border-box;
  width: 320px;
  height: 389px;
  background-image: url(../img/intro-triangle.svg), url(../img/back-desktop.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  padding: 90px 19px 85px 19px;

  @media (min-width: @tablet-width){
    width: @tablet-width;
    height: 518px;
    padding: 78px 153px 90px 153px;    
    // background-image: url(../img/back-tablet.png);
  }

  @media (min-width: @desktop-width) {
    width: @desktop-width;
    padding: 78px 370px 90px 370px;        
    // background-image: url(../img/back-desktop.png);
  }
}

.welcome-painting__sedona-mountains--catalog {
  box-sizing: border-box;
  height: 158px;
  padding: 45px 45px;  

  @media (min-width: @tablet-width){
    height: 301px;
    padding: 120px 154px;     
  }

  @media (min-width: @desktop-width){
    padding: 120px 370px;     
  }  
}

.welcome-painting__hero-block-text {
  @media (min-width:  @tablet-width) {  
  width: 460px;
  height: 350px;
  }
}

.welcome-painting__sedona-text {
  @media (min-width:  @tablet-width) {  
  width: 460px;
  height: 80px;
  }
}

// .welcome-painting__sedona-mountains::after {
.welcome-painting__hero-block-triangle  {
  position: absolute;
  // content: "AAAAAA";
  left: 0;
  bottom: 0;
  // background-image: url(../img/intro-triangle.svg);
  // width: 100%;
  // height: 57px;;
  // background-size: 10px 100%;
  // background-position: center;
  // background-color: white;
  z-index: 9;
  @media (min-width:  @tablet-width) {
    width: @tablet-width;
  }
  @media (min-width:  @desktop-width) {
    width: @desktop-width;
  }
}

.welcome-painting__description {
  display: block;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  text-align: center;
  margin: 32px 18px 0px 18px;
  width: 284px;

  @media (min-width: @tablet-width){
    font-size: 21px;
    width: 520px;
  }

  @media (min-width: @desktop-width) {
    width: 484px;
  }
}

.welcome-painting__justification {
  display: block;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  text-align: center;
  margin-right: 18px;
  margin-left: 18px;
  margin: 35px 18px 16px 18px;
  width: 284px;

  @media (min-width: @tablet-width){
    width: 520px;
  }

  @media (min-width: @desktop-width) {
    width: 484px;
  }
}
