.hotel-block__description-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 320px;
  min-height: 287px;
  align-items: center;

  @media (min-width: @tablet-width) {
    min-width: 768px;
    min-height: 318px;
  }

  @media (min-width: @desktop-width){
    min-width: @desktop-width;
    min-height: 331px;
  }
}


.hotel-block__title {
  text-align: center;
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  color: #000000;
  width: 262px;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    width: 435px;
  }
}

.hotel-block__text {
  text-align: center;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  width: 262px;

  @media (min-width: @tablet-width) {
    font-size: 14px;
  }
}

.hotel-block__button {
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  color: #FFFFFF;
  background-color: #81B3D2;
  padding: 16px 49px;

  @media (min-width: @tablet-width) {
    width: 458px;
  }
}
